body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

header {
	position: -webkit-sticky;
	position: sticky;
}

img {
	width: 80px;
	height: 80px;
}

nav {
	width: 100vw;
}

a {
	letter-spacing: 2px;
	font-size: 20px;
}

i {
	padding-right: 3px;
}

.profilePic {
	width: 10vw;
	height: 30vh;
	float: left;
	padding-right: 2vw;
}

p {
	font-size: 1.3rem;
	line-height: 180%;
	font-family: 'Lato', 'Open Sans';
	padding: 0vh 10vw 2vh 10vw;
	text-align: justify;
}

.p2,
.p3 {
	text-indent: 5em;
}

h2 {
	text-align: center;
	font-family: 'Ubuntu', 'Open Sans';
	margin-bottom: 30px;
}

.aboutMe {
	padding: 30px 50px;
}

.col-sm-3 {
	margin-right: 15px;
}

#scrollTarget {
	overflow: scroll;
	/* width: auto; */
	margin-bottom: 25px;
}

div a .btn btn-primary {
	background-color: rgb(24, 23, 23);
}

.portPics {
	width: 20vw;
	height: 40vh;
}

#port {
	padding: 30px 50px;
}

.orlandoPic {
	width: 25vw;
	height: 40vh;
	float: left;
}

div .leftCol {
	padding: 10px 10px 10px 20px;
}

textarea {
	resize: none;
}

form .btn {
	width: 150px;
	color: white;
	font-size: 20px;
	margin-left: 15px;
}

form p {
	margin-left: 15px;
}

#contact {
	padding: 30px 50px;
}

.info {
	padding: 0;
}

.parallax {
	position: relative;
	opacity: .75;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;
	min-height: 80vh;
}

